:root {
    --bg-color: #000101;
    --text-color: white;
    --secondary-text-color: #FFFEFE;
    --main-color: #E50914;
    --border-color: #232323;
    --accordion-color: #2C2C2D;
}

body {
    background-color: var(--bg-color);
}

main {
    background-image: url("https://assets.nflxext.com/ffe/siteui/vlv3/7ca5b7c7-20aa-42a8-a278-f801b0d65fa1/fb548c0a-8582-43c5-9fba-cd98bf27452f/IN-en-20240326-popsignuptwoweeks-perspective_alpha_website_small.jpg");
    /* background-color: var(--bg-color); */
    color: var(--text-color);
    background-position: top;
    background-repeat: repeat-y;
    /* background-size: cover; */
    /* background-attachment: fixed; */
}

/* ----------Common Style start---------- */
section {
    overflow-x: hidden;
    background-color: var(--bg-color);
    min-height: 75vh;
    border-bottom: .5rem solid var(--border-color);
    padding-bottom: 3rem;
}

section .content h4 {
    font-size: 3.2rem;
    font-weight: 700;
}

section .content p {
    font-size: 1.4rem;
    word-wrap: normal;
}

section .overlay img {
    position: absolute;
    left: 0;
    top: 0;
}

a {
    text-decoration: none;
    color: var(--secondary-text-color);
}

form .form-floating .form-control {
    background: rgba(0, 0, 0, 0.512);
    color: var(--text-color);
}

form .form-floating label::focus {
    background: blue;
}

.content-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn {
    color: white;
    font-weight: 600;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: .2rem solid transparent;
}

.btn-main {
    background-color: var(--main-color);
}

.btn-main:hover {
    background-color: #b4030b;
}

/* .btn-mai */
.step {
    font-size: .8rem;
}

.card {
    text-align: center;
    background: transparent;
    color: var(--secondary-text-color);
}

.card .card-header {
    padding: .2rem;
    font-size: 1.8rem;
    font-weight: 600;
}

.card .card-body {
    /* padding: .2rem; */
    font-size: 1rem;
}

.card button {
    margin-top: 1.5rem;
    width: 100%;
}

/* ----------Common Style end---------- */





/* ----------Header Style start---------- */
header.navbar {
    position: absolute;
    /* position: fixed; */
    width: 100%;
    background: none;
    left: 0;
    top: 0;
}

header.navbar .navbar-brand img {
    height: 3rem;
}

header.navbar .btn-group {
    border: .1rem solid var(--border-color);
}

header.navbar .btn-group button i {
    font-size: 1.5rem;
}

header.navbar .btn-group button i:hover {
    color: white;
}

header.navbar .btn-group select option {
    color: black;
    background: transparent;
}

/* ----------Header Style end---------- */





/* ----------SignUp Style start---------- */
#step1,
#step2,
#step3 {
    min-height: 100vh;
}

#step1 form h4 {
    font-size: 1.8rem;
}

#step1 form p {
    font-size: 1rem;
}

#step1 form input {
    margin-bottom: 1rem;
}

#step1 form button {

    width: 100%;
}



.icon i {
    font-size: 4rem;
    color: var(--main-color);
    margin-bottom: 1rem;
}

#step2 ul.list-group li.list-group-item {
    display: flex;
    background: transparent;
    color: var(--secondary-text-color);
    align-items: center;
    border: none;
}

#step2 ul.list-group li.list-group-item i {
    margin-right: 1.3rem;
    font-size: 2rem;
    color: var(--main-color);
}


#step2 .plan-container {
    display: grid;
    gap: .3rem;
    grid-template-columns: auto auto auto auto;
}

#step2 .plan-container .card ul.list-group li.list-group-item {
    border-bottom: .2rem solid var(--border-color);
}

#step2 .card.plan {
    display: block;
    padding: .5rem;
    text-align: left;
    color: var(--secondary-text-color);
}

#step2 .card.plan .card-header {
    background-image: linear-gradient(-30deg, red, purple 40%, red);
    padding: 1rem;
    border-radius: .5rem;
    font-size: 1rem;
    font-weight: 700;
}

#step2 .card.plan .card-header p {
    font-size: 1.3rem;
    font-weight: 500;
}

#step2 .card.plan .card-body {
    padding: .5rem;
}

#step2 .card.plan .card-body .head {
    font-size: .9rem;
    font-weight: 700;
}

#step2 .card.plan .card-body .value {
    font-size: 1rem;
}

#step3 p {
    font-size: 1.2rem;
}




#login {
    background-image: url("https://assets.nflxext.com/ffe/siteui/vlv3/7ca5b7c7-20aa-42a8-a278-f801b0d65fa1/fb548c0a-8582-43c5-9fba-cd98bf27452f/IN-en-20240326-popsignuptwoweeks-perspective_alpha_website_small.jpg");
    /* background-color: var(--bg-color); */
    color: var(--text-color);
    background-position: top;
    background-repeat: repeat-y;
    min-height: 100vh;
    padding-bottom: 0;
}

#login .overlay {
    width: 100%;
    min-height: 100vh;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0, rgba(0, 0, 0, 0) 60%, rgba(0, 0, 0, 0.9) 100%);
}

#login form {
    color: var(--text-color);
    background-color: rgba(0, 0, 0, 0.753);
    padding: 2.5rem;
}

#login form .form-floating .form-control {
    border-color: grey;
    margin-bottom: 1rem;
}

#login form h4 {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 2rem;
}

#login form button.btn {
    width: 100%;
}

/* ----------Auth Style end---------- */




/* ----------Home Style start---------- */
section#home {
    background: none;
    min-height: 100vh;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0, rgba(0, 0, 0, 0) 60%, rgba(0, 0, 0, 0.9) 100%);
}

#home .home-content {}

#home .home-content h4 {
    font-size: 3rem;
    font-weight: 800;
}

#home .home-content p {
    font-size: 1.3rem;
    line-height: 3.2rem;
}

/* ----------Home Style end---------- */


/* ----------Video Divider Style start---------- */
.video-divider video {
    height: 17rem;

    z-index: 0;

    margin-left: 5rem;
    margin-top: 1.49rem;
}

.video-divider img {
    z-index: 9;
    position: absolute;
    top: -4.7rem;
    left: 0;
}

/* ----------Video Divider Style end---------- */





/* ----------FAQ Style start---------- */
#faq .page-head {
    text-align: center;
    font-size: 3.3rem;
    font-weight: 700;
    color: var(--text-color);
}

#faq .accordion .accordion-item {
    margin-bottom: .7rem;
    background-color: #2C2C2D;
    border: 0;
}

#faq .accordion .accordion-item .accordion-body {
    color: var(--secondary-text-color);
    font-size: 1.4rem;
}

#faq .accordion .accordion-item .accordion-header button {
    background-color: #2C2C2D;
    padding: 1.5rem;
    font-size: 1.5rem;
    color: var(--secondary-text-color);
}

#faq .accordion .accordion-item .accordion-header:hover button {
    background-color: #404041;
}

/* ----------FAQ Style end---------- */








/* ----------Footer Style footer---------- */
footer {
    font-size: .9rem;
    padding: 4rem;
    color: var(--secondary-text-color);
}

footer a {
    text-decoration: underline;
}

footer .list-group-item {
    text-decoration: underline;
    border: 0;
    background: transparent;
    color: var(--secondary-text-color);
}

footer .list-group-item .btn-group {
    border: .1rem solid var(--border-color);
}

/* ----------Footer Style end---------- */